import React from 'react'
import styles from '../Login.module.css'
import VirtualaiLogo from '../../../assets/icons/VirtualAiLogo.png'
import { useHistory } from 'react-router-dom'

function SignInComponent(props) {
  const history = useHistory()

  const handleLogin = () => {
    let path = `/encore-monitoring`
    history.push(path)
  }

  const showForgotPasswordComponent = () => {
    props.showForgotPasswordComponent()
  }

  return (
    <>
      <img
        src={VirtualaiLogo}
        alt="VirtualAiLogo"
        className={styles.vaiLoginLogo}
      />
      <p className={styles.vaiTitleText}>Sign In</p>
      <input
        type="text"
        className={styles.vaiLoginEmailInput}
        placeholder="Email"
      />
      <br />
      <input
        type="password"
        className={styles.vaiLoginPasswordInput}
        placeholder="Password"
      />
      <button className={styles.vaiSignInButton} onClick={handleLogin}>
        Sign In
      </button>
      <p
        className={styles.vaiForgotPasswordText}
        onClick={showForgotPasswordComponent}
        style={{ cursor: 'pointer' }}
      >
        Forgot password?
      </p>
    </>
  )
}

export default SignInComponent
