import React, { useState } from 'react'
import styles from './Profile.module.css'
import profilePhoto from '../../assets/imgs/profile.png'
import chev from '../../assets/icons/Chevron-Right.png'
import hamburgerMenu from '../../assets/icons/Hamburger-Menu.png'

function ProfileComponent(props) {
  const [sidebarState, setSidebarState] = useState(false)

  const toggleSidebar = () => {
    setSidebarState(!sidebarState)
    props.toggleSidebar(!sidebarState)
  }

  return (
    <>
      <div className="row mb-4 pb-1">
        <div className="col-12">
          <img
            src={hamburgerMenu}
            alt="HamburgerMenuIcon"
            className={sidebarState ? 'mx-4' : 'float-end'}
            onClick={toggleSidebar}
            style={{ cursor: 'pointer' }}
          />
        </div>
      </div>
      <div className="mb-3 pb-3 px-3 d-flex position-relative">
        <div className="position-relative">
          <div
            className={`${styles.vaiNotification} ${
              sidebarState ? styles.vaiShrinkNotification : null
            }`}
          >
            <span>2</span>
          </div>
          <span
            className={`${styles.vaiStatus} ${
              sidebarState ? styles.vaiShrinkStatus : null
            }`}
          ></span>
          <img
            src={profilePhoto}
            className={`${styles.vaiProfilePhoto} ${
              sidebarState ? styles.vaiShrinkPhoto : null
            }`}
            alt="ProfilePhoto"
          />
        </div>
        <div
          className={`${
            sidebarState ? styles.vaiHide : null
          } position-relative`}
          style={{ marginLeft: '15px' }}
        >
          <span className={styles.vaiUsername}>Ronald Madrid</span>
          <br />
          <span className={styles.vaiUserTitle}>.NET Developer</span>
        </div>
        <img
          src={chev}
          alt="ChevUpIcon"
          className={`${styles.vaiChevUp} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
        <img
          src={chev}
          alt="ChevDownIcon"
          className={`${styles.vaiChevDown} ${
            sidebarState ? styles.vaiHide : null
          }`}
        />
      </div>
    </>
  )
}

export default ProfileComponent
